.advantages {
  padding-top: 90px;
  padding-bottom: 61px;
  background-image: url(../img/backgraund__adv.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  &__block {
    max-width: 770px;
    display: inline-block;
    align-items: center;
    h4 {
      font-size: 36px;
      font-weight: 500;
      width: 97%;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: normal;
      text-align: left;
      color: #3c4450;
      padding-bottom: 42px; }
    p {
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.63;
      letter-spacing: normal;
      text-align: left;
      color: #707984;
      padding-top: 42px; }
    button {
      margin-top: 41px;
      margin-right: 30px;
      background: none;
      border: 2px solid #27c8ca;
      border-radius: 5px;
      p {
        padding: 22px 21px 21px 16px;
        font-size: 16px;
        font-weight: bold;
        color: #3c4450; } } } }
