.header {
  width: 100%;
  height: 89px;
  position: absolute;
  top: 0;
  left: 0;
}

.header__content {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  padding-left: 32px;
}

.header__logo select {
  width: 127px;
  height: 17px;
  font-family: FiraSans;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.25;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  background: none;
}

.header__entry button {
  background: none;
}

.header__entry button p {
  width: 138px;
  height: 15px;
  font-family: FiraSans;
  font-size: 15px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.4;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}

body {
  font-family: 'Fira Sans', sans-serif;
}

.container {
  width: 100%;
  max-width: 1140px;
  box-sizing: border-box;
  margin: 0 auto;
