.header {
  width: 100%;
  height: 89px;
  position: absolute;
  top: 0;
  left: 0;
  &__content {
    display: flex;
    justify-content: space-between; }
  &__logo {
    padding-left: 32px;
    select {
      width: 127px;
      height: 17px;
      font-family: FiraSans;
      font-size: 16px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 2.25;
      letter-spacing: normal;
      text-align: left;
      color: #ffffff;
      background: none; } }
  &__entry {
    button {
      background: none;
      p {
        width: 138px;
        height: 15px;
        font-family: FiraSans;
        font-size: 15px;
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: 2.4;
        letter-spacing: normal;
        text-align: center;
        color: #ffffff; } } } }

